interface DataItem {
  id: number;
  pid: number;
  username: string;
  createDate: string;
  msg: string;
  childs: Array<any>;
}
export default defineComponent({
  name: "messageList",
  components: {
  },
  props: {
    item: {
      type: Object,
      default: () => {
        return {};
      },
    },
    status: {
      type: String,
    },
  },
  // emits: ['replyshow'],

  setup(props, { emit }) {
    const router = useRouter();
    const data = {
      likes: 0,
      dislikes: 0,
      action: "",
    };
    const replySelect = () => {
      const token = getCookie("token") || sessionStorage.getItem("token");
      if (token) {
        sessionStorage.setItem("replyId", props.item.id);
        replyshow();
      } else {
        router.push({ path: "/login" });
      }
    };
    const replyshow = () => {
      console.log("replyshow");
      //父组件传参
      emit("replyshow", true);
    };
    // const refData = toRefs(data);

    return {
      replySelect,
      replyshow,
      // dayjs,
      // ...refData,
    };
  },
});
